.header {
  font-size: 52px;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 70px;
}

.main {
  display: flex;
  flex-direction: column;
  width: 60vw;
  height: 80%;
  margin: 0 auto;
}

.movie-info {
  display: flex;
  flex-direction: row;
}

.movie-info-img {
  width: 300px;
  height: 350px;
}
.movie-info-contents {
  display: flex;
  flex-direction: column;
  margin-left: 30px;
  font-size: 17px;
}

.movie-info-title,
.movie-rating,
.movie-download {
  margin-bottom: 20px;
}

.movie-info-title {
  font-size: 36px;
  border-bottom: 1px solid black;
  padding-bottom: 10px;
}

.movie-description {
  margin-bottom: 10px;
}

.movie-description-content {
  height: 160px;
  overflow: auto;
}
