.header {
  position: sticky;
  top: 0;
  font-size: 52px;
  text-align: center;
  margin-top: 0px;
  padding: 10px 0;
  background-color: #eeeeee;
}

img {
  width: 200px;
  height: 250px;
}

.movies {
  display: grid;
  grid-template-columns: repeat(4, 200px);
  grid-template-rows: repeat(5, 350px);
  justify-content: space-around;
  width: 80vw;
  height: 100%;
  margin: 0 auto;
}

.movie {
  display: flex;
  flex-direction: column;
}

.movie-title {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

a {
  text-decoration: none;
  color: black;
}
.loading {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translateX(-45%);
  opacity: 0.5;
}
